/* Barra lateral */
.slidebar {
  height: calc(100vh - 58px);
  position: fixed;
  top: 0;
  left: -300px;
  width: 300px;
  background-color: #5271ff;
  transition: left 0.3s ease-in-out;
  padding-top: 10px;
  z-index: 1000;
  overflow-y: auto;
  padding-bottom: 100px;
  opacity: 95%;
}

.slidebar.show {
  left: 0;
}


.menu-item {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 0px 0px;
  margin-bottom: 0px;
  border: 1px solid #5271ff;
  border-radius: 50px;
  background-color: #5271ff;
  cursor: pointer;
  font-family: 'Segoe UI', sans-serif;
  font-size: 14px;
  color: #333;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
  transition: background-color 0.2s ease, transform 0.2s ease;
  max-width: 100%;
  width: 120px;  
  height: 60px;
}


.menu-item:hover {
  background-color: #5271ff;
  transform: translateY(-2px);
}

.menu-item span {
  white-space: nowrap;
  

}



.slidebar .menu-item {
  color: #000000;
  background-color: #5271ff;
  padding: 0px 130px 0px 5px;
  margin-top: 10px;
  margin-left: 25px;
  text-align: left;
  width: 80%;
  font-size: 14px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s, transform 0.3s ease;
}

.slidebar .menu-item:hover {
  background-color: #5271ff;
  color: #5271ff;
  transform: translateX(5px);
}

/* Icono de la barra lateral */
.show-slidebar-button {
  position: absolute;
  top: 20px;
  left: 10000px;
  font-size: 28px;
  background-color: #5271ff;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  z-index: 2000;
  transition: color 0.3s ease, transform 0.2s ease;
}

.show-slidebar-button:hover {
  color: #5271ff;
  transform: rotate(180deg);
  background-color: #5271ff;
}

.logo-slidebar {
  margin-top: 20px;
  margin-left: 18%;
  width: 180px;
}

/* Textos Slidebar */
.slidebar p {
  text-align: center;
  font-size: 15px;
  margin-top: 20px;
  margin-bottom: -10px;
  font-weight: bold;
  color: #000000;
}

/* Textos Slidebar */
.slidebar-user {
  position: relative;
  text-align: center;
  font-size: 15px;
  font-weight: bold;
}

/* ================================
   Slidebar Derecho para Filtros
   ================================ */

.filter-slidebar {
  height: calc(100vh - 58px);
  position: fixed;
  top: 0;
  right: -340px;
  width: 300px;
  background-color: #f4f4f9;
  transition: right 0.3s ease-in-out;
  padding-top: 10px;
  z-index: 1000;
  box-shadow: -4px 0 8px rgba(0, 0, 0, 0.2);
  overflow-y: auto;
  padding-bottom: 100px;
  padding-left: 10px;
  padding-right: 0px;
  opacity: 95%;
}

/* Cuando se active el toggle en móviles */
.filter-slidebar.show {
  right: 0;
}

.filter-slidebar h2 {
  font-size: 20px;
  margin-bottom: 15px;
  text-align: center;
  color: #000000;
}

.filter-slidebar label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  font-size: 14px;
  color: #000000;
  margin-top: 10px;
  margin-left: 2px;
}

.filter-slidebar select {
  width: 95%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 12px;
}

.filter-slidebar input {
  width: 89%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 12px;
}

/* ================================
     Botón para mostrar el Slidebar de Filtros
     ================================ */

.show-filter-slidebar-button {
  height: 40px;
  width: 40px;
  position: absolute;
  top: 20px;
  right: 25px;
  color: black;
  border: none;
  padding: 1px 1px;
  border-radius: 5px;
  cursor: pointer;
  z-index: 2000;
  transition: background-color 0.3s, transform 0.2s;
}

.show-filter-slidebar-button:hover {
  color: #5271ff;
  transform: rotate(360deg);
}
/* Aseguramos que el body no tenga márgenes ni paddings */
html,
body {
  margin: 0;
  padding: 0;
}

/* Tarjeta principal */
.homepage-card {
  box-sizing: border-box; /* Incluye padding y border en el ancho total */
  background: #fff;
  border-radius: 5px;
  box-shadow: 5px 5px 15px rgb(0, 0, 0);
  padding: 10px;
  height: 100%;
  max-height: calc(
    100vw - 2.3cm
  ); /* 100vw menos 1cm total (0.5cm a cada lado) */
  max-width: calc(100vw - 5cm); /* 100vw menos 1cm total (0.5cm a cada lado) */
  width: 100%;
  margin: -9px auto 0; /* Se centra horizontalmente, con margen superior negativo */
  transition: margin-top 0.3s ease-in-out, box-shadow 0.3s ease;
  animation: fadeIn 1s ease-out;
  overflow: visible;
  opacity: 0.95;
}

.homepage-card:hover {
  box-shadow: 0 12px 36px rgba(0, 0, 0, 0.2);
}

/* Fecha */
.current-date {
  font-size: 15px;
  font-weight: bold;
  color: black;
  text-align: left;
  margin-left: 16px;
  margin: 10px 0px -9px 0px;
}

.current-date:hover {
  transition: transform 0.2s ease, color 0.3s ease;
  color: #5271ff;
}

/* Botón para crear tabla */
.button-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 18px;
  padding-right: 20px;
  padding: 2px;
}

.create-table-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #28a745;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 24px;
  transition: background-color 0.3s, transform 0.2s ease-in-out;
  font-weight: 900;
  border-radius: 5px;
  z-index: 1000;
}

.create-table-button:hover {
  background-color: #218838;
  transform: scale(1.05);
}

.create-table-button:active {
  transform: scale(1);
}

.title-page {
  color: #000000;
  text-align: center;
  margin-bottom: -50px;
  margin-left: 0px;
  margin-top: -0px;
  font-size: 25px;
  padding: 0%;
}

.homepage-title {
  margin-bottom: 20px;
  margin-top: -10px;
  margin-left: 5px;
  max-width: 1700px;
  left: 0;
  box-sizing: border-box; /* Incluye padding y border en el cálculo del ancho */
  width: calc(100vw - 2.3cm); /* 100vw menos 1cm total (0.5cm a cada lado) */
  margin: 0 auto; /* Centrado horizontalmente */
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  top: -20px;
  opacity: 0.95;
}
/* Contenedor que garantiza el espacio en laterales y abajo */
.card-wrapper {
  box-sizing: border-box;
  padding: 0 0.5cm 10px; /* 0 en la parte superior, 0.5cm a los lados, 10px abajo */
  width: 100%;
  margin: 0 auto;
}

/* Tabla */
.table-container {
  -webkit-overflow-scrolling: touch;
  background-color: #f9f9f9;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  border-top: 3px solid #5271ff;
  animation: fadeIn 1s ease-out;
  display: block;
  margin-top: 15px;
  border-collapse: collapse;
  table-layout: fixed;
  overflow-x: auto;
  overflow-y: auto;
  touch-action: manipulation;
  max-height: 150%;
  width: calc(100vw - 5.5cm); /* 100vw menos 1cm total (0.5cm a cada lado) */
  max-width: none;
  margin: 0 10px;
  padding: 0px;
  height: calc(10vw - 1.5cm); /* 100vw menos 1cm total (0.5cm a cada lado) */
  margin: 5px 0;
  padding-bottom: 20px;
  box-sizing: border-box;
  min-height: calc(93vh - 149px);
}

.table-container table {
  border-collapse: collapse;
  font-size: 20px;
  table-layout: fixed;
}

thead tr {
  position: sticky;
  top: -1%;
  left: -4%;
  background-color: #fff;
  z-index: 1;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  white-space: nowrap;
}

tr {
  white-space: nowrap;
}

th,
td {
  padding: 8px 0px;
  font-weight: 400;
  word-wrap: break-word;
}

