/*  ─────────────────────────  AgendaExpress‑header.css  v2  ───────────────────── */

@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@400;600;800&display=swap");

/* ========== CONTENEDOR ============================ */
.aexp-header{
  position:absolute;
  top:40px;                         /* ajusta según prefieras            */
  left:50%;
  transform:translateX(-50%);
  text-align:center;
  pointer-events:none;
  user-select:none;
  z-index:2000;
}
























/* ========== TÍTULO ================================= */
.aexp-title {
  /* Tipografía */
  font-family: "Orbitron", sans-serif;
  font-weight: 800;
  font-size: clamp(25px, 5vw, 45px);
  letter-spacing: 0.12em;
  text-transform: uppercase;
  line-height: 1.1;
  
  /* Evitar saltos de línea */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  /* 🎨 Estilos de color */
  /* 1️⃣ Fallback sólido */
  color: var(--neon1, #00eaff);
  text-shadow: 0 0 6px currentColor, 0 0 12px currentColor;

  /* 2️⃣ Holograma si se soporta background‑clip */
  background: linear-gradient(
    130deg,
    var(--neon1, #00eaff),
    var(--neon2, #ff00e0),
    var(--neon3, #7cff00),
    var(--neon4, #ffea00),
    var(--neon5, #00ffe8)
  );
  background-size: 400% 400%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  /* Animación */
  animation: holoShift 12s linear infinite;
  
  /* Posicionamiento */
  position: relative;
}







/*  Glitch SUTIL sin data‑attributes                   */
.aexp-title::before,
.aexp-title::after{
  content:attr(data-glitch) attr(data-glitch, "AGENDA EXPRESS");
  position:absolute;
  top:0; left:0;
  width:100%; height:100%;
  mix-blend-mode:screen;
  pointer-events:none;
}

.aexp-title::before{
  clip-path:polygon(0 2%,100% 2%,100% 18%,0 18%);
  animation:glitchX 3s infinite steps(1,end);
  opacity:.75;
}

.aexp-title::after{
  clip-path:polygon(0 82%,100% 82%,100% 98%,0 98%);
  animation:glitchY 2.8s infinite steps(1,end);
  opacity:.6;
}










/* ========== SUBTÍTULO ============================== */
.aexp-subtitle{
  margin-top:8px;
  font-family:"Orbitron",sans-serif;
  font-weight:600;
  font-size:clamp(14px,2.4vw,22px);
  text-transform:uppercase;

  /* Fallback */
  color:var(--neon3,#7cff00);
  text-shadow:0 0 4px currentColor;

  /* Holograma */
  background:linear-gradient(130deg,
              var(--neon3,#7cff00),
              var(--neon1,#00eaff));
  -webkit-background-clip:text;
  -webkit-text-fill-color:transparent;

  opacity:0;
  transform:translateY(-10px) scale(.95);
  animation:subFade .5s forwards;
}








/* Ajuste de espacio cuando hay subtítulo */
@supports(selector(:has(*))){
  .aexp-header:has(.aexp-subtitle){ padding-bottom:10px; }
}

/* ======== ANIMACIONES ======== */
@keyframes holoShift{
  0%   { background-position:0 50%;   }
  50%  { background-position:100% 50%;}
  100% { background-position:0 50%;   }
}

@keyframes glitchX{
  0%,40%,100% { transform:translateX(0); }
  41%,42%     { transform:translateX(-3px); }
  43%,44%     { transform:translateX(2px);  }
}

@keyframes glitchY{
  0%,55%,100% { transform:translateX(0); }
  56%,57%     { transform:translateX(3px);  }
  58%,59%     { transform:translateX(-2px); }
}

@keyframes subFade{
  to{ opacity:1; transform:translateY(0) scale(1); }
}
















